import { FC, useState } from "react";
import { Layout } from "../components/layout/layout";
import {
  Button,
  Segment,
  Grid,
  Divider,
  Header,
  Icon
} from "semantic-ui-react";
import { NewNadeImagePlaceholder } from "../components/newnade/NewNadeImagePlaceholder";
import { NewNadeGfycatModal } from "../components/newnade/NewNadeGfycatModal";
import { GfyCatPlayer } from "../components/nadepage/GfycatPlayer";

const NewNadePage: FC = () => {
  const [isGfycatModalVisisble, setIsGfycatModalVisisble] = useState(false);
  const [gfycatID, setGfycatID] = useState<string | null>(null);

  return (
    <Layout>
      <div className="nade-new-container">
        <h2>Add new nade</h2>
        <Segment placeholder>
          <Grid columns={2} stackable textAlign="center">
            <Divider vertical>And</Divider>

            <Grid.Row verticalAlign="middle">
              <Grid.Column>
                <Header icon>
                  <Icon name="video" />
                  Gfycat
                </Header>
                <br />
                <Button primary onClick={() => setIsGfycatModalVisisble(true)}>
                  Add
                </Button>
                {gfycatID && <p>{<GfyCatPlayer gfycatID={gfycatID} />}</p>}
              </Grid.Column>

              <Grid.Column>
                <NewNadeImagePlaceholder />
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Segment>
        <Button disabled color="green">
          Submit
        </Button>
      </div>
      <NewNadeGfycatModal
        setGfycat={setGfycatID}
        visible={isGfycatModalVisisble}
        onDismiss={() => setIsGfycatModalVisisble(false)}
      />

      <style jsx>
        {`
          .nade-new-container {
            margin: 18px;
          }
        `}
      </style>
    </Layout>
  );
};

export { NewNadePage };
